.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 1200;
  cursor: pointer;
}

.modal {
  max-width: 60vw;
}

.image {
  display: block;
  width: 100%;
  height: auto;
}
